@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&family=Poppins:wght@400;500;600;700;800&family=Urbanist:wght@400;500;600;700;800&display=swap");


body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #171A26;
}

.font-urbanist {
    font-family: 'Urbanist', sans-serif;
}

.font-inter {
    font-family: 'Inter', sans-serif;
}

.bg-gradient-radial {
    /* background-image: radial-gradient(ellipse at center,
      #171a26 0%,
      rgba(12, 14, 21, 0.67) 100%); */
    background-image: radial-gradient(50% 50% at 50% 50%, #171A26 0%, #0C0E16AA 100%);
    /* background-color: #171A26; */
}

.hide-scrollbar {
    /* For Webkit browsers (Chrome, Safari, etc.) */
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    /* For Internet Explorer and Edge */
    scrollbar-width: none;
    /* For Firefox */
}

/* To hide the scrollbar track and thumb specifically */
.hide-scrollbar::-webkit-scrollbar {
    display: none;
    width: 0;
    height: 0;
}


.text-4xl-responsive {
    font-size: clamp(1.5rem, 5vw, 4rem);
}

.text-6xl-responsive {
    font-size: clamp(2.5rem, 6vw, 6rem);
}

.text-xl-responsive {
    font-size: clamp(1rem, 2.5vw, 1.25rem);
}